@import "../../global.scss";

.portfolio {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    font-size: 50px;
  }

  ul {
    margin: 10px;
    padding: 0;
    list-style: none;
    display: flex;
  }

  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    .item {
      width: 380px;
      height: 220px;
      border-radius: 20px;
      border: 1px solid rgb(240, 239, 239);
      margin: 80px 20px 20px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      position: relative;
      //   transition: all 0.5s ease;
      cursor: pointer;

      h3 {
        position: absolute;
        font-size: 30px;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        background-color: $mainColor;
        img {
          opacity: 0.1;
          z-index: 0;
        }
      }
    }
  }
  a {
    position: relative;
    bottom: 10px;
    left: 50%;
    flex-direction: column;
  }
}
